import React from 'react';
import { Layout, Card, Typography, Space, Tag } from 'antd';
import { RocketOutlined, AppstoreOutlined } from '@ant-design/icons';
import './App.css';

const { Header, Content, Footer } = Layout;
const { Title, Paragraph } = Typography;

// App data - add more apps here as you build them
const apps = [
  {
    id: 1,
    title: 'Test App',
    description: 'A test application showcasing Firebase integration',
    url: '/apps/test-app',
    color: '#1890ff',
    icon: <RocketOutlined />,
    tags: ['Firebase', 'Cloud Run']
  },
  // Add more apps here in the future
];

function App() {
  const handleAppClick = (url) => {
    window.location.href = url;
  };

  return (
    <Layout className="layout">
      <Header className="header">
        <div className="header-content">
          <Space align="center" size="middle">
            <AppstoreOutlined style={{ fontSize: '32px', color: '#fff' }} />
            <Title level={2} style={{ margin: 0, color: '#fff' }}>
              HL App Showcase
            </Title>
          </Space>
        </div>
      </Header>

      <Content className="content">
        <div className="content-wrapper">
          <div className="intro-section">
            <Title level={1} className="main-title">
              Welcome to my apps
            </Title>
            <Paragraph className="main-description">
              A collection of web applications and experiments
            </Paragraph>
          </div>

          <div className="apps-grid">
            {apps.map((app) => (
              <Card
                key={app.id}
                className="app-card"
                hoverable
                onClick={() => handleAppClick(app.url)}
                style={{ borderTop: `4px solid ${app.color}` }}
              >
                <Space direction="vertical" size="middle" style={{ width: '100%' }}>
                  <div className="app-icon" style={{ color: app.color }}>
                    {app.icon}
                  </div>
                  <div>
                    <Title level={3} style={{ marginBottom: 8 }}>
                      {app.title}
                    </Title>
                    <Paragraph type="secondary" style={{ marginBottom: 12 }}>
                      {app.description}
                    </Paragraph>
                    <Space wrap>
                      {app.tags.map((tag) => (
                        <Tag key={tag} color="blue">
                          {tag}
                        </Tag>
                      ))}
                    </Space>
                  </div>
                </Space>
              </Card>
            ))}
          </div>
        </div>
      </Content>

      <Footer className="footer">
        <Paragraph type="secondary" style={{ margin: 0 }}>
          Built with React + Firebase Hosting
        </Paragraph>
      </Footer>
    </Layout>
  );
}

export default App;

